export default class Module {

  constructor(element) {

    this.setup(element)

  }

  initialize() { }

  setupListeners() { }

  setup(element) {

    this.el = element
    this.identifier = element.dataset.module
    this.data = element.dataset
    this.els = {}

    this.setup = this.setup.bind(this)
    this.setupEls = this.setupEls.bind(this)
    this.setupListeners = this.setupListeners.bind(this)

    this.setupEls()
  }

  setupEls() {

    this.el.querySelectorAll(`[data-el^="${this.identifier}"]`)
      .forEach((el, index) => {

        const singleAlias = el.dataset.el.replace(`${this.identifier}.`, "") + "El"
        const pluralAlias = `${singleAlias}s`

        if (!index) this.els[singleAlias] = el

        this.els[pluralAlias] = typeof this.els[pluralAlias] === "array" || []
        this.els[pluralAlias].push(el)
      })

  }

}